<template>
  <div id="app" class="flex-column">
    <router-view class="" v-if="mode === 'testing'" />
    <div v-else-if="mode === 'normal'" class="flex-1 flex-column">
      <nav class="navbar pure-menu pure-menu-horizontal flex-row">
        <ul class="pure-menu-list">
          <li class="pure-menu-item">
            <router-link to="/" class="pure-menu-link">Tests</router-link>
          </li>
          <li class="pure-menu-item">
            <router-link to="/cards" class="pure-menu-link">Cards</router-link>
          </li>
        </ul>
        <ul class="pure-menu-list ml-auto">
          <li v-if="login" class="pure-menu-item">
            <router-link to="/profile" class="inline-block pure-menu-link">Profile</router-link>
          </li>
          <li v-if="login" class="pure-menu-item">
            <a href="#" class="inline-block pure-menu-link" @click="toLogout">logout</a>
          </li>
          <li v-else class="pure-menu-item">
            <router-link to="/Login" class="pure-menu-link">Login</router-link>
          </li>
        </ul>
      </nav>
      <router-view 
      class="relative"
      />
    </div>
  </div>
</template>

<script>

export default {
  data() {
    return {
      // mode: localStorage.getItem('mode') || 'normal',
      mode: window.$mode || 'normal',
      login: false,
    };
  },
  computed: {
  },
  mounted() {
    let user = localStorage.getItem('user');
    if (user) {
      user = JSON.parse(user);
      this.login = true;
      window.$user = user;
    }
    this.$bus.$on('changeMode', this.changeMode);
    this.$bus.$on('login', this.toLogin);
  },
  destroyed() {
    this.$bus.$off('changeMode', this.changeMode);
    this.$bus.$off('login', this.toLogin);
  },

  methods: {
    changeMode(mode) {
      this.mode = mode;
      localStorage.setItem('mode', mode);
    },
    toLogin() {
      debugger;
      this.login = true;
    },
    toLogout() {
      // forget user
      localStorage.clear();
      this.login = false;
    },
  },
};
</script>

