<template>
  <div class="flex-column flex-1 relative">
    <div class="header pure-form flex-row pd-1">
    <input class="flex-item-center" type="text" v-model="keyword" placeholder="Search">
      <h3 v-if="title" class="ml-auto title">
        {{ title }}
      </h3>
    </div>
    <div class="body pd-1 flex-1 flex-column relative" v-loading="loading">
      <no-data v-if="list.length === 0 && !loading">
      </no-data>
      <ul class="list">
        <li
        class="list-item"
        v-for="item in list"
        :key="item.content"
        >
        <a :href="item.to">
          {{ item.content }}
        </a>
        </li>
      </ul>
    </div>
    <div class="footer">
      <pager
      :total="total"
      @click="$emit('click', $event)"
      />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: '',
    },
    list: {
    },
    total: {
      type: Number,
      default: 0,
    },
    loading: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      keyword: '',
    };
  },
  watch: {
    keyword(value) {
      this.$emit('search', value);
    },
  },
};
</script>

<style lang="scss" scoped>
.header {
  background: #ccc;
  // color: #777;
}
.list {
  height: 600px;
}
.list-item {
  margin-bottom: 0.6em;
}
</style>
