import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home/index.vue';

Vue.use(Router);

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/tests/:title',
      name: 'testDetails',
      props: true,
      component: () => import(/* webpackChunkName: "polygon" */ './views/TestDetails/index.vue'),
    },
    {
      path: '/room/:title',
      name: 'room',
      props: true,
      component: () => import(/* webpackChunkName: "polygon" */ './views/Room/index.vue'),
    },
    {
      path: '/room/pratice/:title',
      name: 'pratice-room',
      props: true,
      component: () => import(/* webpackChunkName: "polygon" */ './views/Cards/Pratice/index.vue'),
    },
    {
      path: '/cards',
      name: 'cards',
      component: () => import(/* webpackChunkName: "polygon" */ './views/Cards/index.vue'),
    },
    {
      path: '/cards/:title',
      name: 'card',
      props: true,
      component: () => import(/* webpackChunkName: "polygon" */ './views/Cards/Card/index.vue'),
    },
    {
      path: '/polygon',
      name: 'polygon',
      component: () => import(/* webpackChunkName: "polygon" */ './views/Polygon'),
      children: [
        {
          path: 'tests',
          name: 'polygon-tests',
          component: () => import(/* webpackChunkName: "polygon" */ './views/Polygon/Tests'),
        },
        {
          path: 'cards',
          name: 'polygon-cards',
          component: () => import(/* webpackChunkName: "polygon" */ './views/Polygon/Cards'),
        }
      ]
    },
    {
      path: '/profile',
      name: 'profile',
      component: () => import(/* webpackChunkName: "polygon" */ './views/Profile'),
    },
    {
      path: '/login',
      name: 'login',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "login" */ './views/Login.vue'),
    },
    {
      path: '/register',
      name: 'register',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "login" */ './views/Register.vue'),

    },
    {
      path: '/results/:id',
      name: 'results',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "login" */ './views/Results'),


    },
  ],
});

router.beforeEach((to, from, next) => {
  if (to.name === 'room' || to.name === 'pratice-room') {
    window.$mode = 'testing';
  } else {
    window.$mode = 'normal';
  }
  next();
})

export default router; 
