import Vue from 'vue';
import axios from 'axios';

import App from './App.vue';
import router from './router';
import Alert from './components/alert.vue';
import Checkbox from './components/checkbox.vue';
import Radio from './components/radio.vue';
import Pager from './components/pager.vue';
import Tag from './components/tag.vue';
import Star from './components/star.vue';
import List from './components/list.vue';
import NoData from './components/no-data.vue';
import Tree from './components/tree.vue';
import TreeInput from './components/tree-input.vue';
import Breadcrumb from './components/breadcrumb.vue';
import Comment from './components/comment';
import Rating from './components/rating.vue';
import CardGroup from './components/card-group.vue';
import Sidebar from './components/sidebar.vue';
import Calender from './components/calender.vue';

Vue.component('alert', Alert);
Vue.component('checkbox', Checkbox);
Vue.component('radio', Radio);
Vue.component('pager', Pager);
Vue.component('star', Star);
Vue.component('tag', Tag);
Vue.component('list', List);
Vue.component('no-data', NoData);
Vue.component('tree', Tree);
Vue.component('tree-input', TreeInput);
Vue.component('breadcrumb', Breadcrumb);
Vue.component('my-comment', Comment);
Vue.component('rating', Rating);
Vue.component('card-group', CardGroup);
Vue.component('sidebar', Sidebar);
Vue.component('calender', Calender);


import url from './config/index';

const constants = {
  EACH: 0,
  ALL: 1,
};

Vue.config.productionTip = false;

Vue.prototype.$axios = axios;
Vue.prototype.$url = url;
Vue.prototype.$constants = constants;
Vue.prototype.$TYPES = {
  SINGLE_CHOICE: 'single-choice',
  MULTIPLE_CHOICE: 'multiple-choice',
  OPENING: 'opening',
  BLANK_FILLING: 'blank-filling',
};

Vue.prototype.$alert = (type, message) => {
  const delay = 1300;
  const div = document.createElement('div');
  div.innerHTML = message;
  div.classList.add('alert');
  if (type === 'success') {
    div.classList.add('alert-success');
    const icon = document.createElement('i');
    icon.classList.add('fas', 'fa-check-circle');
    div.prepend(icon);
  } else if (type === 'error') {
    div.classList.add('alert-error');
    const icon = document.createElement('i');
    icon.classList.add('fas', 'fa-exclamation-triangle');
    div.prepend(icon);
  }
  document.body.appendChild(div);
  setTimeout(() => {
    document.body.removeChild(div);
  }, delay);
};

Vue.filter('formatDateTime', (timeStamp) => {
  const date = new Date(timeStamp);
  const year = date.getFullYear();
  let month = date.getMonth() + 1;
  let day = date.getDay();
  let hour = date.getHours();
  let min = date.getMinutes();
  if (month < 10) {
    month = '0' + month;
  }
  if (day < 10) {
    day = '0' + day;
  }
  if (hour < 10) {
    hour = '0'+ hour;
  }
  if(min < 10) {
    min = '0' + min;
  }
  return `${year}-${month}-${day} ${hour}:${min}`;
});
Vue.filter('clock', (ms) => {
  let seconds = Math.floor(ms / 1000);
  let mins = Math.floor(seconds / 60);
  seconds %= 60;
  if (mins < 10) {
    mins = `0${mins}`;
  }
  if (seconds < 10) {
    seconds = `0${seconds}`;
  }
  return `${mins}:${seconds}`;
});

Vue.mixin({
  methods: {
    keepLinebreak(text) {
      return text.replace(/(?:\r\n|\r|\n)/g, '<br>');
    },
    deepCopy(obj) {
      return JSON.parse(JSON.stringify(obj));
    },
    get() {
      return axios.get;
    },
  },
});

Vue.prototype.$bus = new Vue();
Vue.directive('loading', {
  inserted: (el) => {
    const wrapper = document.createElement('div');
    wrapper.className = 'loading-div';
    wrapper.style.position = 'absolute';
    wrapper.style.width = '95%';
    wrapper.style.height = '95%';
    wrapper.style.zIndex = '999';
    wrapper.innerHTML = '<div class="loading"></div>';
    el.appendChild(wrapper);
  },
  update: (el, binding) => {
    if (!binding.value) {
      const els = el.getElementsByClassName('loading-div');
      if (els.length > 0) {
        el.removeChild(els[0]);
      }
    }
  },
});


new Vue({
  router,
  render: h => h(App),
}).$mount('#app');
