<template>
  <div class="shadow-1 flex-1 flex-row h-100" v-loading="loading">
    <template v-if="!loading">
    <left
    class="left"
    @chooseCategory="toChange"
    @change="change"
    />
    <div class="flex-1 pd-1 ml-1">
      <div class="list-header flex-row">
        <breadcrumb 
        :data="category"
        @click="toClick"
        />
        <button class="ml-auto pure-button pure-button-primary" @click="toPolygon">
          Create New Test
        </button>
      </div>
        <ul v-if="tests.length > 0" class="card-list">
          <li class="mt-1" v-for="item in tests" :key="item._id">
            <card :reg="reg" :data="item"/>
          </li>
        </ul>
        <no-data v-else>
        </no-data>
      </div>
      <pager
        class="mt-auto"
        :total="total"
        :len="10"
        @click="toPage"
      />
    </template>
    </div>
  </template>
<script>
import debounce from '@/tools/debounce';

import Card from './components/card.vue';
import Left from './components/left.vue';

export default {
  components: {
    Left,
    Card,
  },
  data() {
    return {
      tests: [],
      loading: true,

      keyword: '',
      reg: '',

      page: 1,
      total: 0,

      category: [],
    };
  },
  watch: {
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData: debounce(function () {
      this.$axios
        .get(`${this.$url.tests}?page=${this.page}&limit=10&keyword=${
          this.keyword
        }&category=${this.category.toString()}`)
        .then((resp) => {
          this.tests = resp.data.results;
          this.total = resp.data.total;
          this.loading = false;
        })
        .catch((err) => {
          alert(err.response.data);
          this.loading = false;
        });
    }, 250),
    toClick(data) {
      this.category.splice(data + 1); 
      this.getData();
    },
    toChange(data) {
      this.category = data;
      this.getData();
    },
    toPage(page) {
      this.page = page;
      this.getData();
    },
    change(data) {
      this.keyword = data;
      this.reg = new RegExp(this.keyword);
      this.getData();
    },
    toPolygon() {
      this.$router.push('/polygon/tests');
    },
  },
};
</script>

<style lang="scss" scoped>
.left {
  width: 200px;
}
.card-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
</style>
