<template>
  <div class="card-container pd-3 flex-row" @click="toClick">
    <div class="flex-1 flex-column">
      <h3 class="title">
        {{ data.title }}
      </h3>
      <small class="text-light">
        by {{ data.author }}
      </small>
      <p class="intro">{{ data.intro }}</p>
      <div class="flex-row mt-1 width-50 flex-wrap">
        <tag
        class="mb-1 mr-1"
        v-for="tag in data.tags"
        :text="tag"
        :key="tag"
        />
      </div>
    </div>
    <!-- <router-link
      class="flex-item-center"
      :to="'/tests/' + encodeURIComponent(data.title)"
    >
    Details
    </router-link> -->
  </div>
</template>

<script>
import debounce from '@/tools/debounce';

export default {
  props: {
    data: {
      type: Object,
      required: true,
    },
    reg: {
      required: true,
    },
  },
  watch: {
    data(value) {
      console.log(value);
    }
  },
  watch: {
    reg() {
      // this.highlight();
    },
  },
  methods: {
    highlight: debounce(function () {
      // highlist matched text.
      if (this.reg) {
        const els = document.getElementsByClassName('title');
        for (let i = 0; i < els.length; i += 1) {
          const text = this.data.title;
          els[i].innerHTML = text.replace(this.reg, match => `<span style="color: yellow">${match}</span>`);
        }
      } else {
        const els = document.getElementsByClassName('title');
        for (let i = 0; i < els.length; i += 1) {
          els[i].innerHTML = this.data.title;
        }
      }
    }, 250),
    toClick() {
      this.$router.push(`/tests/${this.data.title}`);
    }
  },
};
</script>

