<template>
  <div class="flex-column pd-1 left">
    <form action="" class="left-header pure-form">
      <input
      type="text"
      placeholder="Search"
      class="w-100"
      v-model="keyword"
      >
    </form>
    <tree  
    class="mt-1"
    :data="category"
    @click="toClick"
    />
    </div>
  </template>

  <script>
  export default {
    data() {
      return {
        keyword: '',
        category: [],
      };
    },
    watch: {
      keyword(value) {
        this.$emit('change', value);
      },
    },
    mounted() {
      this.$axios.get(this.$url.category)
        .then((resp) => {
        this.category = resp.data;
      })
      .catch((err) => {
        this.$alert('error', err.message);
      });
  },
  methods: {
    addTag(tag) {
      if (this.keyword.indexOf(tag) === -1) {
        this.keyword += `${tag},`;
      }
    },
    toClick(data) {
      this.$emit('chooseCategory', data);
    }
  },
};
</script>

<style lang="scss" scoped>
.left {
  border-right: 2px solid silver;
}

</style>
