<template>
  <div class="flex-column">
    <ul class="comment-list">
      <li
      v-for="item in data" :key="item.createdAt"
      class="mt-1"
      >
        <comment
        :data="item"
         />
      </li>
    </ul>
    <form action="" class="pure-form pure-form-stacked" @submit.prevent="submit">
      <textarea class="w-100" name="comment-input" id="comment-input" cols="30" rows="8"
      minlength="10"
      >
      </textarea>
      <button class="pure-button w-100">
        Post
      </button>
    </form>
  </div>
</template>

<script>
import Comment from './components/comment.vue';

export default {
  components: {
    Comment,
  },
  props: {
    data: {
      type: Array,
      required: true,
    },
  },
  methods: {
    submit() {
      const el = document.getElementById('comment-input');
      if (el.value.trim()) {
        let user = window.$user;
        const payload = {
          username: user.username,
          avatar: user.avatar,
          content: el.value.trim(),
          createdAt: Date.now(),
        }
        el.value = '';
        this.$emit('submit', payload);
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.comment-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
</style>
